pf-page{

  .home {
    width: 100%;
    height: 100%;
    background-color: #001c55;
  }
  .pf-v5-c-masthead__brand {
    display: inline-flex;
    align-self: center;
    width: 50px;
    height: 50px;
  }
  
  div{
    background-image: url(../../server/public/images/logo3.png);
    background-size: contain;
    aspect-ratio: 1/1;
    height: 70px;
    background-repeat: no-repeat;
    background-position: center;
    margin: 10px 20px;
  }


  --page-drawer-panel_max-width : 400px;

  .pf-v5-c-panel__main::part(panel) {
    overflow: var(--pf-v5-c-panel__main--Overflow);
    height: 100%;
    max-height: 100%;
  }

  pf-panel.notification-box {
    position: absolute;
    right: 0;
    background-color: #ffffff;
    width: 350px;
    height: 100%;
    box-shadow: 0px 1px 5px 0px rgba(0,0,0,0.75);
    display: none;
  }
  pf-panel.notification-box.active {
    position: absolute;
    right: 0;
    width: 350px;
    height: 100%;
    box-shadow: 0px 1px 5px 0px rgba(0,0,0,0.75);
    display: block;
  }
  .raw-infos {
    display: flex;
    gap: 10px;
    justify-content: space-between;
    padding: 10px 10px;
    margin: 10px 10px;
    align-items: center;
  }
  .raw-infos:nth-child(odd) {
    background-color: #9e9e9e59;
  }

  .raw-infos:nth-child(even) {
    background-color: #9e9e9e17;
  }
  .down-arrow:nth-child(even) {
    background-color: #ffffff;
    width: 24px;
    height: 24px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 3px;
    cursor: pointer;
  }

  .pf-v5-c-masthead.pf-m-display-inline {
   background-color: #001c55 !important;
  }

}